import React, { Suspense, lazy } from 'react';
import { Route, Switch } from 'react-router-dom';

const DefaultView = lazy(() =>
  import(
    /* webpackChunkName: "challenges-default-details-view" */ './default-details'
  )
);
const ParticipantsView = lazy(() =>
  import(/* webpackChunkName: "challenges-blank-page" */ './participants')
);

const Details = ({ match }) => {
  return (
    <Suspense fallback={<div className="loading" />}>
      <Switch>
        <Route
          path={`${match.url}/:id?/participants`}
          render={(props) => <ParticipantsView {...props} />}
        />

        <Route
          path={`${match.url}/:id?`}
          render={(props) => <DefaultView {...props} />}
        />
      </Switch>
    </Suspense>
  );
};

export default Details;
